html {
  font-family: "Noto Sans TC", "Roboto", sans-serif, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue"; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

a {
  display: block;
  text-decoration: none;
  color: #ffffff; }
  a:hover {
    text-decoration: underline;
    transform: scale(1.05);
    transition: 0.3s; }

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px; }
  @media (max-width: 992px) {
    .container {
      max-width: 720px; } }
  @media (max-width: 767px) {
    .container {
      max-width: 100%; } }

.header {
  background-color: #aa0601; }

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  @media (max-width: 767px) {
    .header-container {
      flex-direction: column; } }
  .header-container .logo {
    padding-top: 16px;
    padding-bottom: 16px; }
    .header-container .logo img {
      width: 80px; }
  .header-container .nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    font-size: 20px;
    text-align: center; }
    @media (max-width: 767px) {
      .header-container .nav {
        width: 100%;
        font-size: 16px; } }
    .header-container .nav li {
      margin-left: 64px; }
      @media (max-width: 767px) {
        .header-container .nav li {
          width: 49%;
          margin-left: 0px;
          padding-top: 12px;
          padding-bottom: 12px;
          border: 1px solid #ffffff; } }

.footer {
  background-color: #aa0601;
  color: #ffffff; }

.footer-container {
  padding-top: 48px;
  padding-bottom: 32px; }
  @media (max-width: 992px) {
    .footer-container {
      padding-bottom: 40px; } }
  @media (max-width: 767px) {
    .footer-container {
      padding-top: 24px;
      padding-bottom: 24px; } }

.footer-content {
  display: flex;
  justify-content: space-between;
  padding-bottom: 23px;
  border-bottom: 1px solid #ffffff; }
  @media (max-width: 767px) {
    .footer-content {
      padding-bottom: 15px; } }
  .footer-content .info .nav {
    display: flex;
    align-items: center;
    height: 40px;
    margin-bottom: 24px; }
    @media (max-width: 767px) {
      .footer-content .info .nav {
        display: none; } }
  .footer-content .info li {
    margin-right: 48px; }
  .footer-content .info .contact {
    display: flex;
    font-size: 36px;
    padding-bottom: 8px; }
    @media (max-width: 767px) {
      .footer-content .info .contact {
        font-size: 16px; } }
  .footer-content .media {
    display: flex; }
    .footer-content .media li {
      margin-left: 16px; }
      @media (max-width: 767px) {
        .footer-content .media li {
          margin-left: 8px; } }
    .footer-content .media img {
      filter: invert(20%); }
      .footer-content .media img:hover {
        filter: invert(0%);
        transform: scale(1.2);
        transition: 0.5s; }
      @media (max-width: 992px) {
        .footer-content .media img {
          filter: invert(0%);
          width: 40px; } }
      @media (max-width: 767px) {
        .footer-content .media img {
          width: 24px; } }

.contact svg {
  width: 48px;
  margin-right: 16px; }
  @media (max-width: 767px) {
    .contact svg {
      width: 24px;
      margin-right: 8px; } }

@keyframes hoverImg {
  0% {
    transform: translateY(0px); }
  20% {
    transform: translateY(-3px); }
  80% {
    transform: translateY(2px); }
  100% {
    transform: translateY(0px); } }

.contact:hover svg {
  animation: hoverImg 0.8s infinite; }

.copyright {
  display: flex;
  justify-content: space-between;
  margin-top: 32px; }
  @media (max-width: 767px) {
    .copyright {
      flex-direction: column;
      margin-top: 16px; } }
  .copyright ul {
    display: flex; }
    @media (max-width: 767px) {
      .copyright ul {
        flex-direction: column; } }
  .copyright li {
    font-size: 14px;
    margin-left: 48px; }
    @media (max-width: 767px) {
      .copyright li {
        font-size: 16px;
        margin-left: 0;
        padding-top: 8px; } }
  @media (max-width: 767px) {
    .copyright a {
      display: inline-block; } }
